// MEDIA QUERY MANAGER
/*
0 - 600px:      Phone
600 - 900px:    Tablet portrait
900 - 1200px:   Tablet landscape
[1200 - 1800] is where our normal styles apply
1800px + :      Big desktop

$breakpoint arguement choices:
- phone
- tab-port
- tab-land
- big-desktop

ORDER: Base + typography > general layout + grid > page layout > components

1em = 16px
*/
@mixin respond($breakpoint) {
    @if $breakpoint == 600 {
        @media only screen and (max-width: 37.5em) {
            @content;
        } //600px
    }
    @if $breakpoint == 900 {
        @media only screen and (max-width: 56.25em) {
            @content;
        } //900px
    }
    @if $breakpoint == 1200 {
        @media only screen and (max-width: 75em) {
            @content;
        } //1200px
    }
    // @if $breakpoint == big-desktop {
    // 	 @media only screen and (min-width: 112.5em) { @content };    //1800
    // }
}

@mixin colorGradient($color-gradient) {
    background-image: $color-gradient;
    -webkit-background-clip: text;
    color: transparent;
}

// Flex positions

@mixin flexCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin flexEnd {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

@mixin flexBetween {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@mixin flexAround {
    display: flex;
    justify-content: space-around;
    align-items: center;
}
@mixin flexEvenly {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

@mixin flexStartCenter {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

/////////////////////////////

@mixin flexColumn {
    display: flex;
    flex-direction: column;
}

@mixin flexColumnStart {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

@mixin flexColumnCenter {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@mixin flexColumnStartCenter {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

@mixin flexColumnCenterStart {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

@mixin flexColumnBetween {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
@mixin flexColumnAround {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

@mixin flexColumnEvenly {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

@mixin absoluteCenter {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@mixin colorGradient($color-gradient) {
    background-image: $color-gradient;
    -webkit-background-clip: text;
    color: transparent;
}

@mixin hideScrollbar {
    //Hide scrollbar
    -ms-overflow-style: none;
    &::-webkit-scrollbar {
        display: none;
    }
    scrollbar-width: none;
}

@mixin bgImage($url, $pos, $size) {
    background-image: url($url);
    background-position: $pos;
    background-size: $size;
}

@mixin bgImageCenter($url) {
    background-image: url($url);
    background-position: center;
    background-size: cover;
}

@mixin bgImageCenter-2($url, $secondImage) {
    background-image: $secondImage, url($url);
    background-position: center;
    background-size: cover;
}
